<template>
  <a-card style='margin-top: 20px'>
    <h3 style='margin-bottom: 15px'>
      合同文件/备表
    </h3>
    <div class='preview-file' v-if="contractInfo.value&&contractInfo.value!=='未知'" @click='onFileDownload'>
      {{ previewOrderFileInfo(contractInfo.value) }}
    </div>
    <h4 v-else>
      无合同文件
    </h4>

    <!--订单详情-->
    <a-modal title="订单详情" v-model='showListForm' :mask-closable='false' :width="800"
             :footer='null'>
      <div v-if='showListForm'>
        <component :is='FileList' :file-value='fileListForm'></component>
      </div>
    </a-modal>

  </a-card>


</template>

<script>
import {JeecgListMixin} from "@/mixins/JeecgListMixin"
import {OrderMixin} from "@/mixins/OrderMixin"
import fileDownload from "js-file-download";
import {downFile2} from "@/api/manage";

const key = "updatable";
const hide = null;
export default {
  name: "contract_info",
  mixins: [JeecgListMixin, OrderMixin],
  props: {
    orderDetail: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      aDescripSize: "middle",
      contractInfo: {},
      showListForm: false,
      fileListForm: []
    }
  },
  created() {
  },
  mounted() {
    this.buildContractInfo()
  },
  watch: {
    orderDetail: function (newval, oldName) {
    }
  },
  methods: {
    buildContractInfo() {
      let formValues = this.buildFormValues(this.orderDetail);
      if (formValues && formValues.length > 0) {
        this.contractInfo = this.buildDetail("DDAttachment_N09RSD011CW0", formValues)
      }
    },
    //文件展示
    previewOrderFileInfo(fileValue) {
      if (!this.isJsonStr(fileValue)) {
        return fileValue
      }
      let value = ""
      let jsonValue = JSON.parse(fileValue)
      if (jsonValue && jsonValue.length > 0) {
        for (let i = 0; i < jsonValue.length; i++) {
          let file = jsonValue[i]
          value += file.fileName + ","
        }
      }
      if (value.length > 0) {
        value = value.substring(0, value.length - 1)
      }
      return value
    },
    onFileDownload() {
      if (this.contractInfo && this.contractInfo.value !== "未知") {
        let jsonValue = JSON.parse(this.contractInfo.value)
        if (jsonValue) {
          if (jsonValue.length === 1) {
            //直接下载
            let jsonVal = jsonValue[0]
            this.downloadFile(this.orderDetail.id, jsonVal)
          } else {
            //弹窗选择下载，可以全选下载
            this.showListForm = true
            this.fileListForm = jsonValue
          }
        }
      }
    },
    downloadFile(procInstId, fileInfo) {
      this.openMessage()
      console.log("procInst", fileInfo)
      this.getFileUrl(procInstId, fileInfo).then((response) => {
        if (response) {
          this.$message.destroy()
          this.downloadByBlob(response, fileInfo.fileName)
        } else {
          this.showErrorMsg("下载出错", 500, true)
        }
      }).catch((err) => {
        this.showErrorMsg(err.errmsg, err.errcode, true)
      })
    },
    showErrorMsg(msg, code, lastIndex) {
      if (lastIndex) {
        this.loading = false
        this.isSync = false
      }
      let errorMsg
      if (code && code != null) {
        errorMsg = "errorCode：" + code + " , errorMsg：" + msg
      } else {
        errorMsg = msg
      }
      this.$message.destroy()
      this.$message.error(errorMsg)
    },
    openMessage() {
      this.hide = this.$message.loading("获取下载链接..", 0);
    },
    /**
     * 获取页面文件名
     * @param url 文件url
     */
    downloadUrlFile(url, fileName) {
      // url = url.replace(/\\/g, "/");
      const xhr = new XMLHttpRequest();
      xhr.open("GET", url, true);
      xhr.responseType = "blob";
      //xhr.setRequestHeader('Authorization', 'Basic a2VybWl0Omtlcm1pdA==');
      xhr.onload = () => {
        if (xhr.status === 200) {
          // 获取文件blob数据并保存
          // this.saveAs(xhr.response, fileName);
        } else {
          console.log("errrr", xhr)
        }
      };
      xhr.send();
    },

    /**
     * URL方式保存文件到本地
     * @param data 文件的blob数据
     * @param name 文件名
     */
    saveAs(data, name) {
      var urlObject = window.URL || window.webkitURL || window;
      var export_blob = new Blob([data]);
      var save_link = document.createElementNS("http://www.w3.org/1999/xhtml", "a")
      save_link.href = urlObject.createObjectURL(export_blob);
      save_link.download = name;
      save_link.click();
    },

    /**
     * 根据文件url获取文件名
     * @param url 文件url
     */
    getFileName(url) {
      var num = url.lastIndexOf("/") + 1
      var fileName = url.substring(num)
      //把参数和文件名分割开
      fileName = decodeURI(fileName.split("?")[0]);
      return fileName;
    }
  }
}
</script>
<style lang="less">
.preview-file {
  color: #0768AB;
  cursor: pointer;
}

.preview-file:hover {
  color: #347DB0;
}

.preview-file:active {
  color: #5B8AAB;
}

</style>
