<template>
  <a-card style='margin-top: 20px'>
    <div v-for='(item,index) in formValues' :key='index'>
      <h3 v-if='index!==0' style='margin-top: 30px'>
      </h3>
      <div style='margin-bottom: 8px'>
        <span class='formName'>{{ item.name }}</span>
        <span class='formValue'>{{ buildStartValue(item) }}</span>
      </div>

      <a-table
          :rowKey="(record,index)=> index"
          ref="table"
          size="middle"
          bordered
          :scroll="buildScrollInfo(item)"
          :columns="buildColumns(item)"
          :dataSource="buildData(item)"
          :pagination="false"
          @change="handleTableChange">
        />
        <template slot='OrderFile' slot-scope='text, record'>
          <div class='dd-cover' @click='onOrderFileClick(record,text)'>
            {{ previewOrderFileInfo(record, text) }}
          </div>
        </template>
      </a-table>
    </div>
  </a-card>
</template>

<script>
import {JeecgListMixin} from "@/mixins/JeecgListMixin"

export default {
  name: "meter_and_parts_info",
  mixins: [JeecgListMixin],
  props: {
    orderDetail: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      aDescripSize: "middle",
      formValues: [],
      dataSource: [],
      scrollInfo: {x: 0, y: 400}
    }
  },
  created() {
  },
  watch: {
    orderDetail: function (newval, oldName) {
    }
  },
  mounted() {
    this.buildForms()
  },
  methods: {
    //文件展示
    previewOrderFileInfo(record, text) {
      let fileInfo = this.json2Object(text, true);
      if (!fileInfo) {
        return text
      }
      if (typeof fileInfo === "string") {
        return text
      }
      let value = fileInfo.fileName
      if (value && value.length > 28) {
        value = value.substring(0, 14) + "..." + value.substring(value.length - 14, value.length)
      }
      return value
    },

    //文件点击事件
    onOrderFileClick(record, text) {
      let fileInfo = this.json2Object(text, true);
      if (!fileInfo) {
        return
      }
      if (typeof fileInfo === "string") {
        return
      }
      this.downloadFile(this.orderDetail.id, fileInfo)
    }
    ,
    /**
     * 下载文件
     * @param procInstId 流程实例id
     * @param fileInfo 下载的文件
     */
    downloadFile(procInstId, fileInfo) {
      this.loading = true
      this.getFileUrl(procInstId, fileInfo).then((res) => {
        this.loading = false
        this.downloadByBlob(res, fileInfo.fileName)
      }).catch((err) => {
        this.loading = false
      })
    }
    ,
    buildStartValue(item) {
      if (item.ext_value && item.ext_value.statValue) {
        if (item.ext_value.statValue.length > 0) {
          let res = "";
          for (let i = 0; i < item.ext_value.statValue.length; i++) {
            let sv = item.ext_value.statValue[i]
            let val = sv.value
            if (!val) {
              val = sv.num
            }
            if (!val) {
              val = 0
            }
            res += sv.label + ":" + val + " "
          }
          return "(" + res + ")"
        }
      }
      return ""
    }
    ,
    handleTableChange(page) {
      this.pageInfo = page
    }
    ,
    buildForms() {
      let totalFormValues = this.buildFormValues(this.orderDetail);
      if (totalFormValues && totalFormValues.length > 0) {
        this.formValues = this.buildWithExtFormValues(totalFormValues)
      }
    }
    ,

    buildScrollInfo(item) {
      let columns = this.buildColumns(item)
      if (!columns) return {x: 1000, y: 400}
      let scrx = this.calculateWidth(columns)
      return {x: scrx, y: 400}
    }
    ,
    buildColumns(form) {
      let data = this.buildDataSource(form)
      if (!data) return null
      if (Array.isArray(data)) {
        if (data.length < 1) {
          return null
        }
      }
      let columns = []
      if (data && data.length > 0) {
        let arrayInfo = data[0]
        if (arrayInfo && arrayInfo.length > 0) {
          for (let i = 0; i < arrayInfo.length; i++) {
            let cl = arrayInfo[i]
            let onceColumn = {
              title: cl.label,
              dataIndex: cl.key,
              align: "center",
              width: this.buildColumnWidth(cl.label, cl.value)
            }
            let renders = this.checkAndSetRender(onceColumn)
            if (renders) {
              onceColumn.scopedSlots = renders[0]
              onceColumn.title = renders[1]
            }
            columns.push(onceColumn)
          }
        }
      }
      //重新拍序，如果列的数量超过8个
      if (columns.length > 8) {
        let newColumns = []
        //基表数量
        let meterCount = this.queryColumn("NumberField_IF0A4MK3APS0", columns)
        if (meterCount) {
          columns.splice(meterCount.index, 1)
          newColumns.push(meterCount.value)
        }
        //基表单价
        let meterPrice = this.queryColumn("MoneyField_UL8YZGXOGY80", columns)
        if (meterPrice) {
          columns.splice(meterPrice.index, 1)
          newColumns.push(meterPrice.value)
        }
        //基表总价
        let meterTotalPrice = this.queryColumn("CalculateField_AVDTK4UABFC0", columns)
        if (meterTotalPrice) {
          columns.splice(meterTotalPrice.index, 1)
          newColumns.push(meterTotalPrice.value)
        }
        //赠送数量
        let giveCount = this.queryColumn("NumberField_1XW21DBA31XC0", columns)
        if (giveCount) {
          columns.splice(giveCount.index, 1)
          newColumns.push(giveCount.value)
        }
        //表计需求总数
        let meterNeedCount = this.queryColumn("CalculateField_1AGXPYH0C9VK0", columns)
        if (meterNeedCount) {
          columns.splice(meterNeedCount.index, 1)
          newColumns.push(meterNeedCount.value)
        }
        //基表方案
        let meterScheme = this.queryColumn("DDSelectField_KZFVELA0TWG0", columns)
        if (meterScheme) {
          if (newColumns.length > 0) {
            let schemeIndex = meterScheme.index
            if (schemeIndex === columns.length - 1) {
              columns = columns.concat(newColumns)
            } else {
              for (let i = 0; i < newColumns.length; i++) {
                columns.splice(schemeIndex + (i + 1), 0, newColumns[i])
              }
            }
          }
        } else {//随便插入了
          if (newColumns.length > 0) {
            columns = newColumns.concat(columns)
          }
        }
      }
      this.scrollInfo.x = this.calculateWidth(columns)
      return columns
    }
    ,

    checkAndSetRender(column) {
      if (!column) return null
      let id = column.dataIndex
      let render = null
      if (id) {
        if (id.indexOf("DDAttachment_") !== -1) {
          render = []
          render[0] = {customRender: "OrderFile"}
          render[1] = column.title
        }
      }
      return render;
    }
    ,

    queryColumn(id, columns) {
      if (!id || !columns || columns.length < 1) return null
      let res = null
      for (let i = 0; i < columns.length; i++) {
        let column = columns[i]
        if (column.dataIndex === id) {
          res = {}
          res.index = i
          res.value = column
          return res
        }
      }
      return res
    }
    ,
    buildData(form) {
      let dataSource = []
      let data = this.buildDataSource(form)
      if (data && data.length > 0) {
        for (let i = 0; i < data.length; i++) {
          let info = data[i]
          if (!info) continue
          let source = {}
          for (let j = 0; j < info.length; j++) {
            var infoJ = info[j];
            source[infoJ.key] = infoJ.value
          }
          dataSource.push(source)
        }
      }
      return dataSource
    }
    ,

    buildDataSource(form) {
      let data = []
      if (form && form.value && form.value.length > 0) {
        for (let i = 0; i < form.value.length; i++) {
          let formValue = form.value[i]
          if (formValue.rowValue) {
            data.push(formValue.rowValue)
          }
        }
      }
      return data
    }
    ,
    calculateWidth(cloumns) {
      let width = 0
      for (let i = 0; i < cloumns.length; i++) {
        width += parseInt(cloumns[i].width)
      }
      return width
    }
  }
}
</script>
<style lang="less">
.formName {
  font-size: 18px;
  color: black;
}

.formValue {
  font-size: 16px;
  color: black;
  margin-left: 10px;
}

.dd-cover {
  color: brown;
  cursor: pointer;
}

.dd-cover:hover {
  color: #A87474;
}

.dd-cover:active {
  color: #666666;
}
</style>
