<template>
  <a-card>
    <a-descriptions :title="title" :size='aDescripSize'>
      <template v-for='(item,index) in processDetail'>
        <a-descriptions-item v-if="item.name&&item.name!==''" :label="item.name" :key='index'>
          {{ item.value }}
        </a-descriptions-item>
      </template>

    </a-descriptions>
  </a-card>
</template>

<script>
import {JeecgListMixin} from "@/mixins/JeecgListMixin"

export default {
  name: "order_detail",
  mixins: [JeecgListMixin],
  props: {
    orderDetail: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      aDescripSize: "middle",
      processDetail: [],
      title: ""
    }
  },
  mounted() {
    this.buildOrderDetail()
  },
  methods: {
    buildOrderDetail() {
      let formValues = this.buildFormValues(this.orderDetail);
      if (formValues && formValues.length > 0) {
        //订单编号
        this.processDetail.push({name: "订单编号", value: this.orderDetail.business_id})
        //合同号
        this.processDetail.push({name: "合同/订单编号", value: this.orderDetail.contract_no})
        //生产状态
        this.processDetail.push({name: "订单状态", value: this.orderDetail.status})
        //发起部门
        let dept = this.buildDetail("DDSelectField_IQN8UHBLW0O0", formValues)
        this.processDetail.push(dept)
        //生成时间
        this.processDetail.push({name: "下单时间", value: this.orderDetail.create_time})
        //预期出货时间
        this.processDetail.push(this.buildDetail("DDDateField_54MW71DFAHW", formValues))
        //客户名称
        let customerInfo = this.buildDetail("TextField-K2AD4O5B", formValues)
        let customerName = ""
        if (customerInfo && customerInfo.value !== "未知") {
          customerName = customerInfo.value
          this.processDetail.push(customerInfo)
        }
        //客户付款
        let customer = this.buildMutilDetail("TextField_5U9J93A89QK0", "DDSelectField_1CT60NZVTPZ40", formValues)
        if (customer && customer.value !== "未知") {
          if (!customerName) {
            customerName = customer.value
          }
          this.processDetail.push(customer)
        }
        //终端用户  TextField_HMYXD9HIM1S0  DDSelectField_2106GYZEB8ZK0
        let remoteCustomer = this.buildMutilDetail("TextField_HMYXD9HIM1S0", "DDSelectField_2106GYZEB8ZK0", formValues);
        if (remoteCustomer && remoteCustomer.value !== "未知") {
          if (!customerName) {
            customerName = remoteCustomer.value
          }
          this.processDetail.push(remoteCustomer)
        }
        //下单联系人
        let orderUser = this.buildDetail("InnerContactField_1O7TEJSL96XS0", formValues)
        this.processDetail.push(orderUser)
        // //联系人电话
        // this.processDetail.push(this.buildDetail("PhoneField_1R0S3R8WYG800", formValues))
        //订单类型
        this.processDetail.push(this.buildDetail("DDSelectField_21ANSA0DFC740", formValues))
        //货币
        this.processDetail.push(this.buildDetail("DDSelectField_U7LQGHVRC6O0", formValues))
        //运送方式
        this.processDetail.push(this.buildDetail("DDSelectField_23S0WEA8ZOM80", formValues))
        //付款方式
        let payModes = this.buildDetail("DDSelectField_1RRO07830CQO0", formValues)
        if (payModes && payModes.value !== "未知") {
          this.processDetail.push(payModes)
        }
        //收款方式
        let payMode = this.buildPaymentMethod(formValues)
        if (payMode) {
          this.processDetail.push(payMode)
        }
        //收款方式描述
        let payDes = this.buildDetail("TextField_NP4BFCY0U2O0", formValues)
        if (payDes && payDes.value !== "未知") {
          this.processDetail.push(payDes)
        }
        //应收预付款
        let prePayMode = this.buildMutilDetail("MoneyField_1W6TMJ9O7SN40", "CalculateField_Z7V79W257400", formValues)
        if (prePayMode && prePayMode.value !== "未知") {
          this.processDetail.push(prePayMode)
        }
        //软件费用
        this.processDetail.push(this.buildDetail("MoneyField_1MIXOZA98IU80", formValues))
        //运输金额
        this.processDetail.push(this.buildDetail("MoneyField_DVG19ZQ4I1S0", formValues))
        //項目金额等级
        this.processDetail.push(this.buildDetail("DDSelectField_MAH9871U6FK0", formValues))
        //項目縂金额
        let amount = this.buildDetail("CalculateField_ECO9DA8P8480", formValues)
        if (amount) {
          if (amount.value === "未知") {
            amount.value = "0"
          }
        }
        this.processDetail.push(amount)
        //构建订单信息
        this.title = this.buildTitle(customerName, orderUser, dept.value)
      }
    },

    buildPaymentMethod(formValues) {
      let res = ""
      let show = ""
      let advanceCollection = this.buildDetail("NumberField_52585AHSIIC0", formValues)
      if (advanceCollection && advanceCollection.value) {
        res += advanceCollection.name + ":" + advanceCollection.value + ","
        if (advanceCollection.value && advanceCollection.value !== "未知") {
          show += advanceCollection.value
        }
      }
      let fahuoqian = this.buildDetail("NumberField_135OUTEYCIE80", formValues)
      if (fahuoqian && fahuoqian.value) {
        res += fahuoqian.name + ":" + fahuoqian.value + ","
        if (fahuoqian.value && fahuoqian.value !== "未知") {
          show += fahuoqian.value
        }
      }
      let fahuohou = this.buildDetail("NumberField_MQPXOHZOX9S0", formValues)
      if (fahuohou && fahuohou.value) {
        res += fahuohou.name + ":" + fahuohou.value + ","
        if (fahuohou.value && fahuohou.value !== "未知") {
          show += fahuohou.value
        }
      }
      let zhibao = this.buildDetail("NumberField_1YCI780G434W0", formValues)
      if (zhibao && zhibao.value) {
        res += zhibao.name + ":" + zhibao.value
        if (zhibao.value && zhibao.value !== "未知") {
          show += zhibao.value
        }
      }
      if (show !== undefined && show !== "") {
        return {
          name: "付款描述",
          value: res
        }
      }
      return null
    },
    /**
     * 构建title 由用户-表-下单人
     */
    buildTitle(customer, user, deptName) {
      if (!customer) {
        customer = "未知"
      }
      if (!deptName) {
        deptName = "未知"
      }
      // let forms = []
      // let count = ""
      // let totalFormValues = this.buildFormValues(this.orderDetail);
      // if (totalFormValues && totalFormValues.length > 0) {
      //   forms = this.buildWithExtFormValues(totalFormValues)
      // }
      // if (forms.length > 0) {
      //   for (let i = 0; i < forms.length; i++) {
      //     let extForm = this.checkAndParse(forms[i].ext_value)
      //     if (extForm && extForm.statValue && extForm.statValue.length > 0) {
      //       let startValues = this.checkAndParse(extForm.statValue)
      //       for (let j = 0; j < startValues.length; j++) {
      //         let val = startValues[j]
      //         let name = val.label
      //         let value = val.num
      //         if (value) {
      //           count += name + ":" + value + ","
      //         }
      //       }
      //     }
      //   }
      // }
      // if (count && count.length > 0) {
      //   count = count.substring(0, count.length - 1)
      // }
      let userVal = user.value && user.value !== undefined ? user.value : "未知"
      // if (count) {
      //   return customer + "-[" + count + "]-" + userVal
      // }
      return customer + " - " + deptName + " - " + userVal
    }

  }
}
</script>
<style lang="less">
</style>
